<template lang="html">
     <header :class="[btnClass,cname]">
       <slot/>
     </header>
</template>

<script>
export default {
    props: {
        cname: {
            type: String,
            default: "",
        },
    },
    data() {
        return {
            btnClass: "btn",
        }
    },
}
</script>

<style lang="scss">
@import "../../css/element.scss";
.btn {
    @include btn;
}
</style>



