import Vue from "vue"
import Router from "vue-router"
import Home from "../home/index.vue"
import Money from "../money/index.vue"
import Whitebar from "../whitebar/index.vue"
import Raise from "../raise/index.vue"
import Download from "../special/download.vue"
import Member from "../member/index.vue"
import "../../css/reset.scss"

Vue.use(Router)

export default new Router({
    routes: [{
        path: "/",
        name: "home",
        component: Home,
    }, {
        path: "/money",
        name: "money",
        component: Money,
    }, {
        path: "/whitebar",
        name: "whitebar",
        component: Whitebar,
    }, {
        path: "/raise",
        name: "raise",
        component: Raise,
    }, {
        path: "/download",
        name: "download",
        component: Download,
    }, {
        path: "/member",
        name: "member",
        component: Member,
    }],
})



